import { Injectable } from '@angular/core';
import { LoginModel } from '../models/login-model';
import { HttpClient } from '@angular/common/http';
import { LoginResponseModel } from '../models/login-response-model';
import {environment} from '../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  constructor(private http: HttpClient) {}

  login(loginModel: LoginModel): Promise<LoginResponseModel> {
    return this.http.post<LoginResponseModel>(`${environment.baseApiUrl}ApplicationUser/Login`, loginModel).toPromise();
  }

  logout() {
    localStorage.removeItem('token');
  }

  public get loggedIn(): boolean {
    return (localStorage.getItem('token') !== null);
  }

  public getLoggedInUser() {
    return this.http.get<any>(`${environment.baseApiUrl}UserProfile`, ).toPromise();
  }
}
