import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { environment } from '../../../../environments/environment';
import { CodeBlock } from '../../../models/codeBlock';
import { CodeSection } from '../../../models/codeSection';
import { CodeSectionType } from '../../../models/codeSectionType';
import { Exercise } from '../../../models/exercise';
import { User } from '../../../models/user';
import { AuthService } from '../../../services/auth.service';
import { Guid } from 'guid-typescript';
import { HttpHeaders } from '@angular/common/http';
import * as moment from 'moment';

@Component({
  selector: 'app-task-details',
  templateUrl: './task-details.component.html',
  styleUrls: ['./task-details.component.scss']
})
export class TaskDetailsComponent implements OnInit {
  baseUrl = `${environment.baseApiUrl}Exercise`;
  id: any;
  createMode: boolean = false;
  task: Exercise;

  htmlCode: CodeSection;
  cssCode: CodeSection;
  jsCode: CodeSection;
  deadline: string;

  constructor(private route: ActivatedRoute,
    private router: Router,
    private authService: AuthService,
    private http: HttpClient) { }

  async ngOnInit() {
    this.id = this.route.snapshot.params['id'];
    if(this.id === 'new'){
      this.createMode = true;
      await this.initTask();
    } else {
      this.http.get(`${this.baseUrl}/${this.id}`).subscribe((task: Exercise) => {
        this.task = task;
        this.deadline = moment(this.task.deadline).format('DD/MM/yyyy');
        this.htmlCode = task.codeSections.find(
          x => x.codeSectionType === CodeSectionType.HTML);
        this.cssCode = task.codeSections.find(
          x => x.codeSectionType === CodeSectionType.CSS);
        this.jsCode = task.codeSections.find(
          x => x.codeSectionType === CodeSectionType.JS);
      });
    }
  }

  onTimeChanged($event){
    this.deadline = $event;
    this.task.deadline = this.deadline;
  }

  async initTask(){
    const user = await this.authService.getLoggedInUser() as User;
    this.task = {} as Exercise;
    this.task.creatorId = user.id;
    this.task.codeSections = [];
    this.htmlCode = {
      codeSectionType: CodeSectionType.HTML
    } as CodeSection;
    this.htmlCode.codeBlocks = [{
      guid: Guid.create()
    } as CodeBlock];
    this.cssCode = {
      codeSectionType: CodeSectionType.CSS
    } as CodeSection;
    this.cssCode.codeBlocks = [
      {
        guid: Guid.create()
      } as CodeBlock
    ];
    this.jsCode = {
      codeSectionType: CodeSectionType.JS
    } as CodeSection;
    this.jsCode.codeBlocks = [{
      guid: Guid.create()
    } as CodeBlock];
  }

  async saveTask(){
    const token = await localStorage.getItem('token');
    const headers = new HttpHeaders({
      'Accept': '*/*',
      'Content-Type':  'application/json',
      Authorization: token 
    });

    this.task.codeSections = [
      this.htmlCode,
      this.cssCode,
      this.jsCode
    ]

    try {      
      if (this.createMode) {
        await this.http.post<any>(`${this.baseUrl}`, this.task, { headers: headers }).toPromise();
      }
      else {
        await this.http.put(`${this.baseUrl}/${this.task.id}`, this.task, { headers: headers }).toPromise();
      }  
    } catch (error) {
      
    } finally{
      this.router.navigate(['/task-management']);
    }
  }

  addJsBlock(){
    this.jsCode.codeBlocks.push({
      guid: Guid.create()
    } as CodeBlock);
  }

  addCssBlock(){
    this.cssCode.codeBlocks.push({
      guid: Guid.create()
    } as CodeBlock);
  }

  addHtmlBlock(){
    this.htmlCode.codeBlocks.push({
      guid: Guid.create()
    } as CodeBlock);
  }

  removeJsBlock(block: CodeBlock){
    this.jsCode.codeBlocks = this.jsCode.codeBlocks.filter(x => x.guid !== block.guid);
  }

  removeCssBlock(block: CodeBlock){
    this.cssCode.codeBlocks = this.cssCode.codeBlocks.filter(x => x.guid !== block.guid);
  }

  removeHtmlBlock(block: CodeBlock){
    this.htmlCode.codeBlocks = this.htmlCode.codeBlocks.filter(x => x.guid !== block.guid);
  }
}
